import React from "react";
import { useState } from "react";
import { useRef } from "react";
import { useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";

let scrollTimeout;
let isObserverOn = true;
let switchModeTimer;

let myScrollTo = (t, h) => {
  return new Promise((resolve, reject) => {
    window.scrollTo({
      behavior: t,
      top: h,
    });
    resolve("scroll!");
  });
};

export default function withScroll(Component, elementHash) {
  return () => {
    const mountRef = useRef(false);
    const location = useLocation();
    const history = useHistory();
    const secRef = useRef(null);
    const loactionRef = useRef(location);
    const historyRef = useRef(history);
    useEffect(() => {
      loactionRef.current = location;
      historyRef.current = history;
      //maintain loacationref used in callback

      if (location.hash === elementHash && location.state?.fromDashboard) {
        //console.log(location.state?.fromDashboard);
        isObserverOn = false;
        const element = secRef.current;
        //console.log(element);
        setTimeout(() => {
          /*window.scrollTo({
            behavior: element ? "smooth" : "auto",
            top: element ? element.offsetTop : 0,
          });*/
          myScrollTo(
            element ? "smooth" : "auto",
            element ? element.offsetTop : 0
          ).then(() => {
            clearTimeout(switchModeTimer);
            switchModeTimer = setTimeout(() => {
              isObserverOn = true;
            }, 1500);
          });
        }, 100);
      }
    }, [location]);

    let updateHistoryTimer;
    function updateHistory(newhash) {
      clearTimeout(updateHistoryTimer);
      updateHistoryTimer = setTimeout(function () {
        if (loactionRef.current.hash !== newhash) {
          historyRef.current.push({
            hash: newhash,
            state: { fromDashboard: false },
          });
        }
      }, 200);
    }

    let scrollObserver = new IntersectionObserver(
      (entries, observer) => {
        for (const entry of entries) {
          /* console.log(
            secRef.current === entry.target,
            entry.target,
            entry.isIntersecting,
            entry.intersectionRatio
          );*/

          /*console.log(
            "current",
            entry.target,
            entry.isIntersecting,
            loactionRef.current.hash,
            elementHash
          );*/
          if (
            entry.isIntersecting &&
            loactionRef.current.hash !== elementHash &&
            isObserverOn
          ) {
            clearTimeout(scrollTimeout);
            scrollTimeout = setTimeout(() => {
              if (mountRef.current) {
                secRef.current.focus();
                updateHistory(elementHash);
              }
            }, 100);
            //observer.unobserve(entry.target);
          } else if (
            !entry.isIntersecting &&
            (loactionRef.current.hash === elementHash ||
              !loactionRef.current.hash)
          ) {
            secRef.current.blur();
            clearTimeout(updateHistoryTimer);
          }
        }
      },
      { rootMargin: "-30% 0px -30% 0px" }
    );

    useEffect(() => {
      scrollObserver.observe(secRef.current);
      mountRef.current = true;
      return () => {
        if (scrollObserver) scrollObserver.disconnect();
        mountRef.current = false;
      };
    }, []);

    return (
      <section id={elementHash.replace("#", "")} ref={secRef}>
        <Component />
      </section>
    );
  };
}
