import React from "react";
import { navLinks, socialLinks } from "./NavData";
import Icon from "../Icon";
import { Link, NavLink, useHistory, useLocation } from "react-router-dom";
import { useState } from "react";
import "./index.scss";
import { useDispatch, useSelector } from "react-redux";
import { isHashMatch } from "../../../core/utils";

const NavbarIcons = () => (
  <div className="navbar__nav-icons">
    {socialLinks.map(({ label, url, icon }) => (
      <a
        key={label}
        className="navbar__nav-icon-link"
        href={url}
        //open in new tag
        target="_blank"
        // for security !!!!
        // hide the site info to scam website
        rel="noopener noreferrer"
      >
        <Icon className="navbar__nav-icon" icon={icon} />
      </a>
    ))}
  </div>
);

export default React.memo(function Navbar() {
  let history = useHistory();
  let location = useLocation();
  const dispatch = useDispatch();
  const showMenu = useSelector((state) => state.app.isMenuOpen);

  return (
    <header>
      <Icon
        className="navbar__icon__home"
        icon="home"
        onClick={() => {
          history.push("/");
          window.scrollTo("smooth", 0);
        }}
      />

      <Icon
        className="navbar__icon__menu"
        icon="menu"
        onClick={() => {
          dispatch({ type: "TOOGLE_MENU" });
        }}
      />

      <nav className={"navbar__nav" + (showMenu ? "" : " hidden")}>
        <div className="navbar__nav-list">
          {navLinks.map(({ label, pathname, hash }) => (
            <NavLink
              exact={true}
              className="navbar__nav-link"
              activeClassName="navbar__nav-link--active"
              isActive={(match) => isHashMatch(match, hash, location)}
              key={label}
              to={{ pathname, hash, state: { fromDashboard: true } }}
            >
              {label}
            </NavLink>
          ))}
        </div>
        <NavbarIcons />
      </nav>
    </header>
  );
});

