import { CSSTransition } from "react-transition-group";

import { Provider } from "react-redux";
import store from "../core/store";

import { BrowserRouter, Route, Switch } from "react-router-dom";
import Navbar from "../view/components/Navbar";
import { routeList } from "../view/components/Navbar/NavData";
import { Suspense, lazy } from "react";

import "./styles.scss";
import AboutDetail from "../view/pages/AboutDetail/AboutDetail";
import Footer from "../view/components/Footer/Footer";
import Resume from "../view/pages/Resume";

function App() {
  return (
    <Provider store={store}>
      <div className="App">
        <BrowserRouter>
          <Navbar />
          {/* put navlinks in BrowserRouter to use location context!!!!*/}
          {[...routeList.keys()].map((pathname) => {
            //console.log(pathname);
            return (
              <Suspense key={pathname} fallback={<div>loading...</div>}>
                <Route exact path={pathname}>
                  {({ match }) => (
                    <CSSTransition
                      in={match != null}
                      timeout={300}
                      classNames="page"
                      unmountOnExit
                    >
                      <div className="page">
                        {routeList.get(pathname).map((Component, idx) => {
                          //console.log(idx);
                          //use match function to control unmount animation of previous route
                          //but each key of child component is required to be unique for not to be influenced by ohther routes

                          return <Component key={idx + pathname} />;
                        })}
                      </div>
                    </CSSTransition>
                  )}
                </Route>
              </Suspense>
            );
          })}
          <Route exact path="/about-detail">
            <AboutDetail></AboutDetail>
          </Route>
          <Route exact path="/resume">
            <Resume />
          </Route>
          <Footer></Footer>
        </BrowserRouter>
      </div>
    </Provider>
  );
}

export default App;
