import * as React from "react";
import Button, { Props as ButtonProps } from "../Button/Button";
import "./SvgButton.scss";

export default function SvgButton({
  Icon,
  label,
  onClick,
  className,
  onClickSvg,
  type = "button",
}) {
  let [ison, switchOn] = React.useState(false);
  return (
    <div
      className={className + ` SvgButton__container SvgButton__${label}`}
      onClick={() => {
        switchOn(!ison);
      }}
    >
      <Button
        className={`btn__icon btn__${label}`}
        onClick={onClick}
        type={type}
        title={label}
      >
        <Icon ison={typeof Icon === "function" ? ison : undefined} />
      </Button>
    </div>
  );
}
