const routeList = new Map();
navLinks.reduce((routeList, item) => {
  if (!routeList.has(item.pathname)) routeList.set(item.pathname, []);

  routeList.get(item.pathname).push(
    withScroll(
      lazy(() => import(`../../${item.Component}`)),
      item.hash
    )
  );
  return routeList;
}, routeList);

